            content: "\"Yosh Tadqiqotchi\" ( Young Researcher - Молодой Исследователь ) jurnali har oyda bir marotaba elektron shaklda chop etiladigan milliy jurnal hisoblanadi.   “Yosh Tadqiqotchi” jurnali quyidagi yo'nalishlar bo'yicha tadqiqot natijalarini qamrab oladi:"
