import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import {API_PATH} from "./const";
import axios from "axios";

const Books = () => {

    const [books, setBooks] = useState([])
    const getBooks = () => {
        axios.get(API_PATH + `items/books`)
            .then(res => {
                setBooks(res.data.data.results)
            })
    }
    useEffect(() => {
        getBooks()
    }, [])


    return (
        <div className="books">
            <div className="container d-flex justify-content-between mt-5">
            </div>
            <div className="container">
                <div className="row">
                    <div className="col-md-9">
                        <div className="d-flex justify-content-between">
                            <h2 className="mt-3">Kitoblar</h2>
                        </div>
                        <div className="row">
                            {
                                books?.map(item =>(
                                    <div className="col-md-4">
                                        <div className="boxes">
                                            <img src={item.file}/>
                                            <p className="text-white" >{item.date}</p>
                                            <div>
                                                <Link to={`/detail-global/${item.id}`}  >{item.name}</Link>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                    <div className="col-md-3">
                        <div className="d-flex justify-content-between">
                            <h2 className="mt-3">Top</h2>
                        </div>
                        <div className="row">
                            {
                                books?.reverse().slice(0,6).map(item =>(
                                    <div className="col-md-12">
                                        <div className="mini-boxes">
                                            <img src={item.file}/>
                                            <div>
                                                <p className="text-white" >{item.date}</p>
                                                <Link to={`/detail-global/${item.id}`}  >{item.name.length > 30  ? item.name.slice(0, 22) + "...."   : item.name}</Link>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Books;