import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import axios from "axios";
import {API_PATH} from "./const";

const Documents = () => {
    const [books, setBooks] = useState([])
    const books2 = [
        {
            id: 1,
            name: "Adliya vazirligi hokim qarorlarini elektron qabul qilish tizimini ishga tushirdi",
            date: "12-12-2022",
            file: "/images/hhu1.jpg",
            content: "Huquqiy davlat va fuqarolik jamiyatining shakllanishida qonunchilikning maqbulligi, mukammalligi, oshkoraligi, xalqchilligi va samaradorligini taʼminlash muhim ahamiyatga ega. Shu bois, mahalliy norma ijodkorligi jarayonini yana-da takomillashtirish, ushbu sohaga zamonaviy axborot kommunikatsiya texnologiyalarini joriy qilinishi bugungi kundagi dolzarb masalalardan biri hisoblanadi."
        },
        {
            id: 2,
            name: "“E-qaror” elektron tizimi orqali qabul qilingan ilk qaror Qashqadaryo viloyati hokimiga tegishli boʻldi",
            date: "31-10-2022",
            file: "/images/hu22.jpg",
            content: "Adliya vazirligi Jamoatchilik bilan aloqalar boʻlimining xabar berishicha, Oʻzbekiston tarixida ilk bor viloyat hokimining rasmiy qarori elektron raqamli imzo orqali birorta qogʻoz ishlatilmasdan “E-qaror” elektron tizimi orqali qabul qilindi."
        }

    ]

    const getBooks = () => {
        axios.get(API_PATH + `items/documents`)
            .then(res => {
                setBooks(res.data.data.results)
            })
    }
    useEffect(() => {
        getBooks()
    }, [])
    return (
        <div className="documents">
            <div className="container">
                <h2 className="mt-5">Hujjatlar</h2>
                <div className="row">
                    <div className="col-md-12">
                        {
                            books?.map(item =>(
                                <div className="col-md-12">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <img src={item.file}/>
                                        </div>
                                        <div className="col-md-8">
                                            <h3>{item.name}</h3>
                                            <span>{item.date}</span>
                                            <p className="text-white" >{item.content.length > 200 ? item.content.slice(0, 200) + "..." : item.content}</p>
                                            <Link to={`/detail-documents-global/${item.id}`}  >Batafsil...</Link>

                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Documents;