import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import axios from "axios";
import {API_PATH} from "./const";

const Events = () => {
    const [books, setBooks] = useState([])
    const books2 = [
        {
            id: 1,
            name: "Bugun FA Fundamental kutubxonasida «OʼzR FА Fundamental kutubxonasi maʼmuriyati va kasaba uyushmasi qoʼmitasi oʼrtasida 2020-2022 yillarga moʼljallab tuzilgan jamoa shartnomasini 2021 yilda bajarilishi» mavzusida uchrashuv bo’lib o’tdi!",
            date: "12-12-2022",
            file: "/images/t1.jpg",
            content: "Uchrashuvga FA Birlashgan kasaba uyushmasi hisobchisi Begzod Roziqov tashrif buyurdilar, kutubxona rahbariyati va kasaba uyushmasi raisi Eldor Sobitov o'z hisoboti bilan ishtirok etdilar. Uchrashuv kutubxona xodimlar jamoasi bilan savol-javoblarga boy, samimiy suhbat tariqasida tashkil qilindi."
        },
        {
            id: 2,
            name: "“FA Fundamental kutubxonasi barcha hamyurtlarimiz qatori, barchaning qalbiga iliqlik quvonch olib keluvchi, xalqimiz suyib qadrlovchi Navruz bayramini nishonlashdilar!",
            date: "31-10-2022",
            file: "/images/t2.jpg",
            content: "Bayramda kutubxona rahbariyati va hodimlar jamoasi bilan bir qatorda O'zbekiston Davlat Madaniyat va San'at instituti talabalari tashrif buyurishdi. Sahnada kutubxonamiz hodimlari o'z sahna ko'rinishlari bilan barchaga kulgi ulashishdi, institut talabalari tomonidan kuylangan ashulalar esa bayramga yanada bayramona shukuh bag'ishladi."
        }

    ]
    const getBooks = () => {
        axios.get(API_PATH + `items/events`)
            .then(res => {
                setBooks(res.data.data.results)
            })
    }
    useEffect(() => {
        getBooks()

    }, [])
    return (
        <div className="documents">
            <div className="container">
                <h2 className="mt-5">Tadbirlar</h2>
                <div className="row">
                        {
                            books?.map(item =>(
                                <div className="col-md-12 mt-5">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <img className="w-100" src={ item.file }/>
                                        </div>
                                        <div className="col-md-8">
                                            <h3>{item.name?.length > 100 ? item.name?.slice(0, 100) + "..." : item.name}</h3>
                                            <span>{item.date}</span>
                                            <p className="text-white" >{item.content?.length > 200 ? item?.content?.slice(0, 200) + "..." : item.content}</p>
                                            <Link to={`/detail-events/${item.id}`}  >Batafsil...</Link>

                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                </div>
            </div>
        </div>
    );
};

export default Events;