import React, {useEffect, useState} from 'react';
import axios from "axios";
import {API_PATH} from "./const";
import {Modal, ModalBody, ModalFooter} from "reactstrap";
import {useHistory} from "react-router-dom";

const DetailNewsGlobal = (props) => {
    const [item, setItem] = useState({})
    const [modal, setModal] = useState(false)
    const [modal2, setModal2] = useState(false)
    const [forFile, setforFile] = useState(item.src)
    const [forFile2, setforFile2] = useState(item.file)
    const [auth, setAuth] = useState(false)

    const item2 = {
            id: 1,
            name: "Popda kamondan otish bo‘yicha musobaqa o‘tkazildi",
            date: "12-12-2022",
            file: "/images/n1.jpg",
            content: "Namangan viloyati Pop tumanida joylashgan 1-sonli BO‘SMda O‘zbekiston Respublikasi Milliy gvardiyasi Namangan viloyati Qo‘riqlash boshqarmasi hamda viloyat Turizm va sport bosh boshqarmasi bilan hamkorlikda kamondan otish sport turi bo‘yicha musobaqa o‘tkazildi. Unda ishtirokchilar ustozlari tomonid...\n" +
                "\n"
        }
    let history = useHistory();
    const toggle = () => {
        setModal(!modal)
    }
    const toggle2 = () => {
        setModal2(!modal2)

    }

    const pic = (e) => {
        let data = new FormData()
        data.append("image", e.target.files[0])
        axios.post(API_PATH + "files", data)
            .then(res =>{
                setforFile(res.data.data.id)
            })
    }
    const pic2 = (e) => {
        let data = new FormData()
        data.append("image", e.target.files[0])
        axios.post(API_PATH + "files", data)
            .then(res =>{
                setforFile2(res.data.data.id)
            })
    }
    const postBook = (data) => {
        axios.patch(API_PATH + "items/news/" + props.match.params.id, {
            "name": document.getElementById('n6').value,
            "content": document.getElementById("n7").value,
            "src" : forFile,
            "file": forFile2,
            "date" : item.date
        })
            .then(res => {
                console.log(res)
                setModal(false)
                axios.get(API_PATH + "items/news/" + props.match.params.id)
                    .then(res=>{
                        setItem(res.data.data)
                    })
            })
    }
    const deleteFc = () => {
        axios.delete(API_PATH + "items/news/" + props.match.params.id)
            .then(res=>{
                history.push("/personal-news")
            })
    }
    useEffect(()=>{
        console.log(props.match.params.id)
        axios.get(API_PATH + "items/news/" + props.match.params.id)
            .then(res=>{
                setItem(res.data.data)

            })


        axios.get(API_PATH + "users")
            .then(res =>{
                console.log(res.data.data)
                res.data.data.map(item => {

                    if (item.email === localStorage.getItem("email")   )
                    {
                        setAuth(true)
                    }
                })
            })
    } , [])
    return (
        <div className="detail pb-5 min-vh-100">
            <div className="container pt-5">
                <div className="row">
                    <div className="col-md-4">
                        <img className="w-100" src={item2.file}/>

                    </div>
                    <div className="col-md-8">
                        <h2>{item2.name} </h2>
                        <p className="text-white">{item2.content}</p>
                    </div>
                </div>
            </div>



            <Modal isOpen={modal} toggle={toggle} className={props.className}>

                <div className="d-flex justify-content-between w-100 pl-3 pt-2 pr-3 align-items-center">
                    <h4 className="m-0">Hujjatni kiriting</h4>
                    <button onClick={toggle} type="button" className="close" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>

                <ModalBody>
                    <input
                        name="name"
                        type="text"
                        className="form-control"
                        // value={item.name}
                        placeholder="Yangilik nomi"
                        required
                        id="n6"
                    />
                    <textarea
                        name="content"
                        // placeholder="Hujjat haqida"
                        placeholder="Yangilik haqida"
                        type="text"
                        className="form-control mt-3"
                        required
                        id="n7"
                    />
                    <input
                        name="src"
                        className="form-control mt-3"
                        placeholder="Yangilikning rasmi"
                        type="file"
                        required
                        onChange={pic}
                        id="n8"
                    />
                    <input
                        name="file"
                        className="form-control mt-3"
                        placeholder="Yangilik faylini kiriting"
                        type="file"
                        required
                        onChange={pic2}
                        id="n9"
                    />
                </ModalBody>
                <ModalFooter>
                    <button type="submit" className="btn btn-primary" onClick={postBook}>Saqlash</button>
                    <button type="button" className="btn btn-danger" onClick={toggle}>Bekor qilish</button>
                </ModalFooter>
            </Modal>

            <Modal isOpen={modal2} toggle={toggle2} className={props.className}>

                <ModalBody>
                    <h2>Rostdan ham o'chirishni istaysizmi</h2>
                </ModalBody>
                <ModalFooter>
                    <button type="submit" className="btn btn-danger" onClick={deleteFc}>O'chirish</button>
                    <button type="button" className="btn btn-secondary" onClick={toggle2}>Bekor qilish</button>
                </ModalFooter>
            </Modal>


        </div>
    );
};

export default DetailNewsGlobal;