import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import {Modal, ModalBody, ModalFooter} from "reactstrap";
import axios from "axios";
import {API_PATH} from "./const";

const News = (props) => {
    const [books, setBooks] = useState([])
    const books2 = [
        {
            id: 1,
            name: "Popda kamondan otish bo‘yicha musobaqa o‘tkazildi",
            date: "12-12-2022",
            file: "/images/n1.jpg",
            content: "Namangan viloyati Pop tumanida joylashgan 1-sonli BO‘SMda O‘zbekiston Respublikasi Milliy gvardiyasi Namangan viloyati Qo‘riqlash boshqarmasi hamda viloyat Turizm va sport bosh boshqarmasi bilan hamkorlikda kamondan otish sport turi bo‘yicha musobaqa o‘tkazildi. Unda ishtirokchilar ustozlari tomonid...\n" +
                "\n"
        },
        {
            id: 2,
            name: "Davlatimiz gerbi – faxrimiz, g’ururimiz ramzi",
            date: "31-10-2022",
            file: "/images/n2.jpg",
            content: "⚡️O’zbekiston Respublikasi Davlat Gerbi – mamlakatimiz davlat mustaqilligining ramzlaridan biridir. G’ururimiz va iftixorimiz timsoli bo’lgan davlat Gerbining 30 yilligi nishonlanmoqda. Bugun Urganch tumani Axborot kutubxona markazi hodimi tumandagi Barkamol avlod bolalar ijodiyot maktabida bo’lib “Yosh musavvirlar” to’garak a’zolari bilan “Gerbimiz – faxrimiz” deb nomlangan davra suxbati tashkil etildi. Markaz hodimi Davlatimiz Gerbi to’g’risida yosh musavvirlarga ma’lumot berdi. To’garak a’zolariga davlat Geribi rasmini tasvirlash toshirig’i berildi. Davlat ramzlari tasvirlangan Gerbimiz suratini chiroyli chizgan yoshlarga kutubxonamiz tomonidan kitoblar sovg’a qilindi."
        },
        {
            id: 3,
            name: "Yo’l yo’lakay kitob o’qiymiz",
            date: "03-11-2022",
            file: "/images/n3.jpg",
            content: "Urganch tumani axborot-kutubxona markazi hodimlari tomonidan Qorovul qishlog’ining markaziy ko’chalarida “Yo’l yo’lakay kitob o’qiymiz” loyihasi asosida kitob taqdimotlari o’tkazildi. Yo’lovchilarga markazda yaratilgan imkoniyatlar, kitob fondi va yangi kelgan adabiyotlar haqida ma’lumotlar berildi."
        }
    ]


    const getBooks = () => {
        axios.get(API_PATH + `items/news`)
            .then(res => {
                setBooks(res.data.data.results)
            })
    }
    useEffect(() => {
        getBooks()


    }, [])

    return (
        <div className="documents">
            <div className="container">
                <div className="container d-flex justify-content-between">
                    <h2 className="mt-5">Yangiliklar</h2>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        {
                            books?.map(item => (
                                <div className="col-md-12 ">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <img src={item.file}/>
                                        </div>
                                        <div className="col-md-8">
                                            <h3>{item.name?.length > 100 ? item.name?.slice(0, 100) + "..." : item.name}</h3>
                                            <span>{item.date}</span>
                                            <p className="text-white">{item.content?.length > 200 ? item?.content?.slice(0, 200) + "..." : item.content}</p>
                                            <Link to={`/detail-news-global/${item.id}`}>Batafsil...</Link>

                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>

        </div>
    );
};

export default News;